@import "./scss/config";

@import "./scss/menu";

@import "./scss/overlay";

@import "./scss/background";

* {
  box-sizing: border-box;
}

body {
  @include background;
  background: $primary-color;
  color: #fff;
  margin: 0;
  height: 100%;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;
}

//Header.js
.Header {
  position: fixed;
  //left:20px;
  z-index: 2;
  width: 100%;
  //test out the background area as follow:
  //background-color: purple;
}
.text-secondary {
  color: $secondary-color;
}
//main (h1 SomeOne,h2 small heading)

//Main.js
.Main {
  padding: 4rem;
  //height: 100vh;
   width: 100vw;
  //background-color: red;

  .icons {
    margin: 1rem;

    a {
      padding: 0.5rem;

      &:hover {
        color: $secondary-color;
        @include easeOut();
      }
    }
  }
  &.Main {
    overflow: hidden;
    h1 {
      margin-top: 20vh;
    }
  }
}
//main.js headings
h1,
h2
 {
  margin: 0px;
  font-weight: 400;
  width:100%;

  &.lg-heading {
    font-size: 6rem;
  }

  &.sm-heading {
    margin-bottom: 2rem;
    padding: 0.2rem 1rem;
    //text-align: center;
    overflow: hidder;
    background: rgba(lighten($primary-color, 1), 0.5);
  }
}

a {
  color: white;
  text-decoration: none;
}
